import React,{useState} from "react";
import Prompt from "./Prompt";
import UseOnEnter from "./UseOnEnter";
import MapConsoleOutput from "./MapConsoleOutput";
const Console=()=>{
  const inputText=React.useRef();
  const [consoleOutput,onEnter]=UseOnEnter();
  const [theFile,onFileChanges]=useState("");
  React.useEffect(()=>{
    inputText.current.value="";
    inputText.current.focus();
  });
  function onChange(e){
    const render=new FileReader();
    render.readAsText(e.target.files[0]);
    render.onload=(e)=>{onFileChanges(e.target.result);};
  }
  return (
    <section className="console">
      <MapConsoleOutput consoleOutput={consoleOutput}/>
      <div className="input-prompt">
        <Prompt/>
        <input
          type="text"
          ref={inputText}
          onKeyPress={({target:{value},key})=>onEnter(value,theFile,key)}
        />
        <div>
          <label htmlFor="files">File Picker</label>
          <input id="files" type="file" className="file-input-prompt" accept=".gcode" onChange={onChange}/>
        </div>
      </div>
    </section>
  );
};
export default Console;
